/* --- Загальні стилі --- */
header > div {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__main {
    display: flex;
    align-items: center;
}

.header__link {
    border-radius: 8px;
    padding: 4px 10px;
    height: 35px;
    color: #000;
    display: block;
}

/* --- Список меню --- */
.header__list {
    display: none;
}

@media screen and (min-width: 1440px) {
    .header__list {
        display: flex;
        gap: 32px;
        margin-left: 432px;
        padding-top: 22px;
    }
}

/* --- Кнопки входу / реєстрації --- */
.button__link {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    padding: 4px 10px;
    height: 35px;
}

.button__link:first-child {
    color: #000;
    background-color: rgba(0, 0, 0, 0.05);
    width: 58px;
}

.button__link:last-child {
    color: #fff;
    background-color: #4169e1;
    width: 113px;
}

.header__buttons {
    display: none;
}

@media screen and (min-width: 1440px) {
    .header__buttons {
        display: flex;
        gap: 32px;
    }
}

/* --- Мобільне меню --- */
.mobile {
    display: flex;
    align-items: center;
    gap: 16px;
}

@media screen and (min-width: 1440px) {
    .mobile {
        display: none;
    }
}

.mobile__link {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    padding: 4px 10px;
    height: 35px;
    color: #fff;
    background-color: #4169e1;
    width: 113px;
}
@media screen and (min-width: 1440px) {
    .mobile__link {
        width: 182px;
    }
}

/* --- Повноекранне мобільне меню --- */
.container__div {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.98);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 32px;
    transform: translateX(-100%);
    transition: transform 0.4s ease-in-out;
    z-index: 1000;
}

.container__div.active {
    transform: translateX(0);
}

/* Кнопка закриття */
.closeMenu {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 32px;
    background: none;
    border: none;
    cursor: pointer;
}

/* Пункти меню */
.container__list {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

.container__link {
    font-size: 24px;
    color: #333; 
    text-decoration: none;
    font-weight: 500;
}

.container__link:hover {
    color: #4169e1;
}

/* Заборона скролу при відкритому меню */
.noScroll {
    overflow: hidden;
}
